<template>
	<view>
		<uni-nav-bar fixed="true" left-icon="back" left-text="返回" @clickLeft="BackPage"
			title="提现" background-color="#e7ebed" :status-bar="true"></uni-nav-bar>
		<uni-card title="提现金额">
			<text class="text-gray">当前余额{{balance}}元</text>
			<view style="display: flex; font-size: 40upx; margin-top: 10px;">
			￥<input type="digit" v-model="amount" focus="true" style="font-size: 40upx;"
			 @input="input"/>
			 </view>
			<view style="margin-top: 10px; margin-bottom: 20px;">
				<view>
					<text class="text-gray">最低可提现{{least}}元，提现手续费{{rate*100}}%</text>
				</view>
				<view>
					<text class="text-gray">提现实际到账{{amount*(1-rate)}}元</text>
				</view>
				<view v-if="errorMsg">
					<text class="text-red">{{errorMsg}}</text>
				</view>
			</view>
			<button type="primary" class="bg-green" @click="withdraw" :disabled="disableWithdraw">提现</button>
		</uni-card>
		<uni-popup ref="popup" type="center">
		    <uni-popup-message :type="infoType" title="提示" :duration="2000" 
					:message="infoContent"  ></uni-popup-message>
		</uni-popup>
	</view>
</template>

<script>
	import common from '@/pages/api/common.js'
	import withdrawapi from '@/pages/api/withdrawapi.js'
	import {uniNavBar, uniList,uniListItem,uniListChat,uniCard,uniPopup,uniPopupMessage} from '@dcloudio/uni-ui'
	
	export default {
		name: 'bill',
		components: {uniList,uniListItem,uniListChat,uniNavBar,uniCard,uniPopup,uniPopupMessage},
		data() {
			let data = {
				rate: 0,
				least: 0,
				amount: '',
				balance: 0,
				errorMsg: '',
				disableWithdraw: true,
				infoContent: '',
				infoType: 'success'
			};
			withdrawapi.init(data)
			return data
		},
		methods: {
			BackPage() {
				common.BackPage()
			},
			input() {
				if (this.amount < this.least) {
					this.errorMsg = '提现金额不能少于' + this.least + '元'
					this.disableWithdraw = true
				} else if (this.amount > this.balance) {
					this.errorMsg = '余额不足'
					this.disableWithdraw = true
				} else {
					this.errorMsg = ''
					this.disableWithdraw = false
				}
			},
			withdraw() {
				if (this.amount >= this.least && this.amount <= this.balance) {
					withdrawapi.withdraw(this.amount, this.$data);
					this.infoContent = '成功提现' + this.amount + '元'
					this.$refs.popup.open()
					this.amount = ''
					this.disableWithdraw = true
					withdrawapi.init(this.$data)
				}
			}
		}
	}
</script>

